<template>
  <Layout title="登录密码" :has-nav="false">
    <div class="password">
      <div class="password-form">
        <input v-model="oldPassword"
               type="password" placeholder="请输入原密码">
        <input v-model="firstPassword"
               type="password" placeholder="请输入密码">
        <input v-model="confirmPassword"
               type="password" placeholder="再次确认密码">
      </div>
      <button :disabled="check" class="submit" @click="submit">提交</button>
    </div>
  </Layout>
</template>

<script>
export default {
  name: 'PasswordView',
  data() {
    return {
      oldPassword:'',
      firstPassword: '',
      confirmPassword: '',
      inputType: 'password'
    }
  },
  computed: {
    check() {
      if (this.firstPassword || this.confirmPassword) {
        return this.firstPassword !== this.confirmPassword
      } else {
        return true
      }
    }
  },
  methods: {
    submit() {
      console.log(this.firstPassword, this.confirmPassword)
    }
  }
}
</script>

